@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/* General Styles */

.body-font{
    font-family: 'Share Tech Mono', monospace;
}

button:hover{
    cursor: pointer;
}

button:active{
    outline-color: #F6C42E;
}

header{
    font-weight: bold;
}

ul{
    padding: 0;
    margin: 0;
}

li{
    list-style: none;
}

.close-btn{
    padding: 0.5em;
    position: fixed;
    left: 0;
    top: 40vh;
    z-index: 20;
    font-family: 'Paytone One', sans-serif;
    background-color: #F6C42E;
    color: #3C599F;
    border: 2px outset rgb(180, 137, 5);
}

#pokedex{
    background-color: #c61433;
    position: relative;
    z-index: 999;
}

#pokedex-header{
    font-family: 'Paytone One', sans-serif;
    height: 10vh;
    margin: 0;
    padding: 1em;
    font-size: 3em;
    color: #F6C42E;
    text-shadow: -1px 1px 2px #3C599F,
                 1px 1px 2px #3C599F,
                 1px -1px 2px #3C599F,
                 -1px -1px 2px #3C599F;
}
/* End Pokedex Component Styles  */


/* FindOrDisplayPokemon Styles */
.pokemon-list-item{
    font-weight: bold;
    padding: 1em;
    border: 1px solid black;
}

.pokemon-list-item:hover{
    cursor: pointer;
    color: #F6C42E;
}


.list-or-search-button{
    height: 100%;
    width: 100%;
    font-weight: bold;
    font-size: 1.25em;
    background-color: inherit;
    border: none;
}

.list-or-search-button:hover{
    color: #F6C42E;
}

#f-o-d-elmt{
    margin-left: 10vw;
    height: 70vh;
    width: 80vw;
    background-color: #9b142a;
    border: 1px solid black;
    position: absolute;
    overflow: scroll;
}

.f-o-d-elmt-child{
    width: 100%;
    height: 100%;
    overflow: hidden;
    position: relative;
    z-index: 50;
}
.f-o-d-elmt-child div{
    width: 100%;
    height: 50%;
    background-color: #9b142a;
    border: 1px solid black;
}

#search-label{
    font-size: 1.2em;
    margin-top: 1em;
    font-weight: bold;
}

#search-label, #search-input{
    display: block;
}

#search-input{
    width: 60%;
    margin: 1em 20%;
    border-radius: 5px;
    padding: 0.5em;
    text-align: center;
}

#search-input:focus{
    outline-color: #F6C42E
}




/* End FindOrDisplayPokemon Styles */

